import React from 'react'
import styled from 'styled-components'
import { useFormatMessage } from 'react-intl-hooks';
import Link from "../Link";
import { getPoolLink, getSwapLink } from "../../utils";

const Footer = styled.div`
    position: fixed;
    bottom: 10px;
    left: -12px;
    width: 100%;
`
const Text = styled.div`
    display: flex;
    justify-content: space-between;
    margin: 0 12px;
`
const Button = styled.div`
  background: ${({theme}) => theme.bg8};
  color: ${({theme}) => theme.white};
  min-width: fit-content;
  width:150px;
  height:40px;
  line-height: 40px;
  text-align: center;
  border-radius: 4px;
  white-space: nowrap;
`
const Bottom = ({address})=> {
    const t = useFormatMessage()
    return (
        <Footer>
            <Text>
                <Link external href={getPoolLink(address)}>
                    <Button>{t({id: 'marketMakers'})}</Button>
                </Link>
                <Link external href={getSwapLink(address)}>
                    <Button>{t({id: 'trade'})}</Button>
                </Link>
            </Text>
        </Footer>
    )
}


export default Bottom
