import React, { useState, useEffect } from 'react'
import dayjs from 'dayjs'
import LocalLoader from '../LocalLoader'
import utc from 'dayjs/plugin/utc'
import { Box, Text } from 'rebass'
import styled from 'styled-components'
import { useFormatMessage } from 'react-intl-hooks';
import InfiniteScroll from "react-infinite-scroller";

import { CustomLink } from '../Link'
import { withRouter } from 'react-router-dom'
import { formattedNum } from '../../utils'
import DoubleTokenLogo from '../DoubleLogo'
import FormattedName from '../FormattedName'
import SwapImg from '../../assets/swap.png'

dayjs.extend(utc)

const List = styled(Box)`
  -webkit-overflow-scrolling: touch;
`

const DashGrid = styled.div`
  display: flex;
  line-height: 35px;
  padding: 0 10px;
  margin-bottom: 5px;
  background: ${({theme})=> theme.bg11};
  border-radius: 8px;
  flex-direction: column;
`

const ListWrapper = styled.div`
  display:flex;
  flex-direction:column;
  align-items:center;
`

const ClickableText = styled(Text)`
  color: ${ ({ theme }) => theme.text10 };
  text-align: start;
  user-select: none;
`

const DataText = styled.div`
  display: flex;
  justify-content: space-between;
  color: ${ ({ theme }) => theme.white };
`
const ValueText = styled.div`
  color: ${({theme})=> theme.text7};
  font-weight: 500;
  font-size: 14px;
  font-family: Helvetica;
`

function PairList({ pairs, color, maxItems = 10 }) {
    const t = useFormatMessage()
    // pagination
    const ITEMS_PER_PAGE = maxItems
    const [ page, setPage ] = useState(1)
    const [ hasMore, setHasMore ] = useState(true)
    const [ dataList, setDataList ] = useState([])
    // sorting
    useEffect(() => {
        setPage(1)
    }, [ pairs ])

    const ListItem = ({ pairAddress }) => {
        const pairData = pairs[pairAddress]
        const [ swapStatus, setSwapStatus ] = useState(true)

        if (pairData && pairData.token0 && pairData.token1) {
            const { token0, token1, reserve0, reserve1, reserveUSD, oneDayVolumeUSD } = pairData
            const liquidity = formattedNum(reserveUSD, true)
            const volume = formattedNum(oneDayVolumeUSD, true)

            // rates
            const token0Rate = reserve0 && reserve1 ? formattedNum(reserve1 / reserve0) : '-'
            const token1Rate = reserve0 && reserve1 ? formattedNum(reserve0 / reserve1) : '-'

            // formatted symbols for overflow
            const formattedSymbol0 = token0?.symbol.length > 6 ? token0?.symbol.slice(0, 5) + '...' : token0?.symbol
            const formattedSymbol1 = token1?.symbol.length > 6 ? token1?.symbol.slice(0, 5) + '...' : token1?.symbol

            return (
                <DashGrid>
                    <DataText area="name" fontWeight="500">
                        <CustomLink to={ '/pair/' + pairAddress } color={ color }
                                    style={ { display: 'flex', justifyContent: 'start', lineHeight: '35px' } }>
                            <DoubleTokenLogo size={ 20 } a0={ pairData.token0.id } a1={ pairData.token1.id } top={'8px'}
                                             margin={ true }/>
                            <FormattedName text={ pairData.token0.symbol + '-' + pairData.token1.symbol }
                                           maxCharacters={ 16 } adjustSize={ true } link={ true }/>
                        </CustomLink>
                        <ValueText>
                            {
                                swapStatus ? <>{ token1Rate } { formattedSymbol0 }/{ formattedSymbol1 }</> : <>{ token0Rate } { formattedSymbol1 }/{ formattedSymbol0 }</>
                            }
                            <img src={ SwapImg } onClick={ () => {
                                setSwapStatus(!swapStatus)
                            } } alt="" style={ { width: '20px', paddingLeft: '10px', verticalAlign: ' sub' } }/>
                        </ValueText>
                    </DataText>
                    <DataText>
                        <ClickableText area="liq">{t({id: 'liquidity'})}</ClickableText>
                        <ValueText area="liq" style={ { justifyContent: 'flex-end' } }>{ liquidity }</ValueText>
                    </DataText>
                    <DataText>
                        <ClickableText area="vol">{t({id: 'volume24'})}</ClickableText>
                        <ValueText area="vol" style={ { justifyContent: 'flex-end' } }>{ volume }</ValueText>
                    </DataText>
                </DashGrid>
            )
        } else {
            return ''
        }
    }
    // 加载更多时 传参 参考m端滚动加载
    useEffect(() => {
        const pairList =
            pairs &&
            Object.keys(pairs)
                .sort((addressA, addressB) => {
                    const pairA = pairs[addressA]
                    const pairB = pairs[addressB]
                    return parseFloat(pairA['trackedReserveUSD']) > parseFloat(pairB['trackedReserveUSD']) ? -1 : 1
                }).slice(ITEMS_PER_PAGE * (page - 1), page * ITEMS_PER_PAGE)
                .map((pairAddress, index) => {
                    return (
                        <div key={ (page - 1) * ITEMS_PER_PAGE + index + 1 }>
                            <ListItem key={ (page - 1) * ITEMS_PER_PAGE + index + 1 }
                                      index={ (page - 1) * ITEMS_PER_PAGE + index + 1 } pairAddress={ pairAddress }/>
                        </div>
                    )
                })
        if (page === 1) {
            setDataList(pairList)
        } else {
            setDataList(L => {
                return [ ...L, ...pairList ]
            })
        }
    }, [ ITEMS_PER_PAGE, pairs, page ])


    const handleInfiniteOnLoad = () => {
        if (Object.keys(pairs).length === 0) return;
        if (!hasMore) return;
        if (dataList.length >= Object.keys(pairs).length) {
            setHasMore(false);
            return;
        }
        setPage(page + 1)
    };
    return (
        <ListWrapper>
            <List style={{ width: '100%', height: '440px', overflow: 'auto' }} p={ 0 }>
                { dataList.length === 0 ? <LocalLoader/> : (
                    <InfiniteScroll
                        initialLoad={false}
                        pageStart={0}
                        loadMore={handleInfiniteOnLoad}
                        hasMore={hasMore}
                        useWindow={false}
                    >
                        {dataList}
                    </InfiniteScroll>
                )}
            </List>
        </ListWrapper>
    )
}

export default withRouter(PairList)
