import React from 'react'
import { useFormatMessage } from 'react-intl-hooks';
import styled from 'styled-components'
import { RowBetween } from '../Row'
import { useGlobalData } from '../../contexts/GlobalData'
import { formattedPercent, localNumber } from '../../utils'

const Header = styled.div`
  width: 100%;
  position: sticky;
  top: 0;
`
const HeaderBox = styled.div`
  width: 100%;
  padding: 10px;
  background: rgba(255,255,255,1);
  border-radius: 15px;
  color: ${({ theme }) => theme.black};
`
const HeaderTitle = styled.div`
  font-size: 14px;
  margin: 10px 0;
  font-family: PingFangSC, PingFangSC-Regular;
  font-weight: 400;
  color:${({ theme }) => theme.text9};
`
const HeaderValue = styled.div`
  font-size: 24px;
  color: #333333;
  font-family: AvenirNext, AvenirNext-DemiBold;
  font-weight: DemiBold;
`
const Medium = styled.span`
  font-size: 14px;
`
export default function GlobalStats() {
    const t = useFormatMessage()
    const { oneDayVolumeUSD, volumeChangeUSD, liquidityChangeUSD, totalLiquidityUSD } = useGlobalData()
    return (
        <Header>
            <HeaderBox>
                <HeaderTitle>{t({id: 'liquidity'})}</HeaderTitle>
                <RowBetween>
                    <HeaderValue>${localNumber(totalLiquidityUSD)}</HeaderValue>
                    <HeaderValue><Medium>{formattedPercent(liquidityChangeUSD)}</Medium></HeaderValue>
                </RowBetween>
                <HeaderTitle>{t({id: 'volume24'})}</HeaderTitle>
                <RowBetween>
                    <HeaderValue>${localNumber(oneDayVolumeUSD)}</HeaderValue>
                    <HeaderValue><Medium>{formattedPercent(volumeChangeUSD)}</Medium></HeaderValue>
                </RowBetween>
            </HeaderBox>
        </Header>
    )
}
