import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import { ApolloProvider } from 'react-apollo'
import { client } from './apollo/client'
import { Route, Switch, BrowserRouter, Redirect } from 'react-router-dom'
import GlobalPage from './pages/GlobalPage'
import TokenPage from './pages/TokenPage'
import PairPage from './pages/PairPage'
import { useGlobalData, useGlobalChartData } from './contexts/GlobalData'
import { isAddress } from './utils'

import { OVERVIEW_TOKEN_BLACKLIST, PAIR_BLACKLIST } from './constants'
import LocalLoader from './components/LocalLoader'
import { useLatestBlock } from './contexts/Application'
import { useDarkModeManager, useLanguage } from './contexts/LocalStorage'
import { IntlProvider } from "react-intl-hooks";
import locale_en from './lang/en.json';
import locale_zh from './lang/zh.json';
import bridge from "./utils/bridge";
const data = {
    en: locale_en,
    zh: locale_zh,
};

const AppWrapper = styled.div`
  position: relative;
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
`
const ContentWrapper = styled.div`
    display: flex;
    max-width: 100vw;
    width: 100%;
    overflow: hidden;
    margin: 0 auto;
`


const Center = styled.div`
  height: 100%;
  width: 100%;
  z-index: 9999;
  transition: width 0.25s ease;
  background-color: ${({ theme }) => theme.bg10};
`

/**
 * Wrap the component with the header and sidebar pinned tab
 */
const LayoutWrapper = ({ children, savedOpen, setSavedOpen }) => {
  return (
    <>
      <ContentWrapper open={savedOpen}>
        <Center id="center">{children}</Center>
      </ContentWrapper>
    </>
  )
}
bridge()
function App() {
    const [, toggleDarkMode] = useDarkModeManager()
    const [language, setLanguage] = useLanguage()
    const [savedOpen, setSavedOpen] = useState(false)
    const [languageStatus, setLanguageStatus] = useState()
    const [mode, setMode] = useState('')
    const globalData = useGlobalData()
    const globalChartData = useGlobalChartData()
    const latestBlock = useLatestBlock()
    const query = new URLSearchParams(window.location.search)
    const dark = query.get('dark')
    const lang = query.get('lang')
    useEffect(()=>{
        if (!dark || mode) return
        toggleDarkMode(dark === '1')
        setMode('true')
    },[mode, toggleDarkMode,dark])
    useEffect(()=>{
        if (!lang || languageStatus) return
        if (lang === 'zh-CN') {
            setLanguage('zh')
        } else {
            setLanguage('en')
        }
        setLanguageStatus('true')
    },[languageStatus, lang, setLanguage])

  return (
          <ApolloProvider client={ client }>
              <IntlProvider
                  locale={ language|| 'en' }
                  messages={{
                      ...data[language || 'en']
                  }}
                  defaultLocale="en"
              >
              <AppWrapper>
                  { latestBlock &&
                  globalData &&
                  Object.keys(globalData).length > 0 &&
                  globalChartData &&
                  Object.keys(globalChartData).length > 0 ? (
                      <BrowserRouter>
                          <Switch>
                              <Route
                                  exacts
                                  strict
                                  path="/token/:tokenAddress"
                                  render={ ({ match }) => {
                                      if (OVERVIEW_TOKEN_BLACKLIST.includes(match.params.tokenAddress.toLowerCase())) {
                                          return <Redirect to="/home"/>
                                      }
                                      if (isAddress(match.params.tokenAddress.toLowerCase())) {
                                          return (
                                              <LayoutWrapper savedOpen={ savedOpen } setSavedOpen={ setSavedOpen }>
                                                  <TokenPage address={ match.params.tokenAddress.toLowerCase() }/>
                                              </LayoutWrapper>
                                          )
                                      } else {
                                          return <Redirect to="/home"/>
                                      }
                                  } }
                              />
                              <Route
                                  exacts
                                  strict
                                  path="/pair/:pairAddress"
                                  render={ ({ match }) => {
                                      if (PAIR_BLACKLIST.includes(match.params.pairAddress.toLowerCase())) {
                                          return <Redirect to="/home"/>
                                      }
                                      if (isAddress(match.params.pairAddress.toLowerCase())) {
                                          return (
                                              <LayoutWrapper savedOpen={ savedOpen } setSavedOpen={ setSavedOpen }>
                                                  <PairPage pairAddress={ match.params.pairAddress.toLowerCase() }/>
                                              </LayoutWrapper>
                                          )
                                      } else {
                                          return <Redirect to="/home"/>
                                      }
                                  } }
                              />

                              <Route path="/home">
                                  <LayoutWrapper savedOpen={ savedOpen } setSavedOpen={ setSavedOpen }>
                                      <GlobalPage/>
                                  </LayoutWrapper>
                              </Route>

                              <Redirect to="/home"/>
                          </Switch>
                      </BrowserRouter>
                  ) : (
                      <LocalLoader fill="true"/>
                  ) }
              </AppWrapper>
            </IntlProvider>
          </ApolloProvider>
  )
}

export default App
