import React, { useEffect, useState } from 'react'
import { withRouter, useHistory } from 'react-router-dom'
import styled from 'styled-components'
import { useFormatMessage } from 'react-intl-hooks';

import { RowFixed } from '../components/Row'
import { AutoColumn } from '../components/Column'
import PairList from '../components/PairList'
import TopTokenList from '../components/TokenList'
import TxnList from '../components/TxnList'
import SupportText from '../components/SupportText'
import GlobalChart from '../components/GlobalChart'
import Search from '../components/Search'
import GlobalStats from '../components/GlobalStats'

import { useGlobalTransactions } from '../contexts/GlobalData'
import { useAllPairData } from '../contexts/PairData'
import Panel from '../components/Panel'
import { useAllTokenData } from '../contexts/TokenData'
import { ThemedBackground } from '../Theme'
import { transparentize } from 'polished'

import { PageWrapper, ContentWrapper } from '../components'
import TxnType from "../components/TxnType";
import {useQuery} from './../hooks'

const HeadText = styled.div`
  font-size: 17px;
  font-family: PingFangSC, PingFangSC-Regular;
  font-weight: 400;
  color: ${({ theme }) => theme.text7};
  text-align: center;
  margin-top: 10px;
`

const SortText = styled.button`
  cursor: pointer;
  font-weight: ${ ({ active }) => (active ? 500 : 400) };
  margin-right: 20px !important;
  border: none;
  border-bottom: 1px solid ${ ({ active, theme }) => (active ? theme.text8 : 'none') };
  background-color: transparent;
  font-size: 14px;
  padding: 0 0 5px;
  color: ${ ({ active, theme }) => (active ? theme.text8 : theme.text6) };
  outline: none;

  @media screen and (max-width: 600px) {
    font-size: 14px;
  }
`

function GlobalPage() {
    const t = useFormatMessage();
    const tabType = new Map(
        new Set([
            ["1", t({id: 'overView'})],
            ["2", t({id: 'pair'})],
            ["3", t({id: 'token'})]
        ])
    )
    // get data for lists and totals
    const allPairs = useAllPairData()
    const allTokens = useAllTokenData()
    const transactions = useGlobalTransactions()
    const [ txFilter, setTxFilter ] = useState('All')
    const { type } = useQuery();
    const router = useHistory();
    const [ tab, setTab ] = useState('1')
    const [activeFlag, setActiveFlag] = useState(true);

    useEffect(() => {
        document.querySelector('body').scrollTo({
            behavior: 'smooth',
            top: 0
        })
        let isUnmounted = false;
        if(!isUnmounted && activeFlag && type) {
            setTab(type)
            setActiveFlag(false)
        }
        router.replace(`/home?type=${tab}`)
        // eslint-disable-next-line
        return () => {
            isUnmounted = true;
        }

    }, [tab, type, activeFlag, router])

    return (
        <PageWrapper>
            <ThemedBackground backgroundColor={ transparentize(0.9, '#389e0f') }/>
            <ContentWrapper>
                <div>
                    <AutoColumn gap="24px">
                        <div style={ { display: 'flex', justifyContent: 'center' } }>
                            <HeadText>{t({ id: "publicPool" })}</HeadText>
                        </div>
                        <Search/>
                        <RowFixed area="txn" gap="20px" pl={ 4 } style={ { paddingBottom: '20px' } }>
                            <SortText onClick={ () => {
                                setTab('1')
                            } } active={ tab === '1' }>{ tabType.get('1') }</SortText>
                            <SortText onClick={ () => {
                                setTab('2')
                            } } active={ tab === '2' }>{ tabType.get('2') }</SortText>
                            <SortText onClick={ () => {
                                setTab('3')
                            } } active={ tab === '3' }>{ tabType.get('3') }</SortText>
                        </RowFixed>
                    </AutoColumn>
                </div>
            </ContentWrapper>
            {
                tab === '1' ? (
                    <ContentWrapper>
                        <div>
                            <AutoColumn gap="24px">
                                <div style={ { padding: '0 20px 24px 0' } }>
                                    <GlobalStats/>
                                </div>
                            </AutoColumn>
                            <AutoColumn style={ { marginTop: '6px' } } gap="8px">
                                <Panel style={ { height: '100%', minHeight: '300px' } }>
                                    <GlobalChart display="liquidity"/>
                                </Panel>
                            </AutoColumn>
                            <TxnType active={txFilter} setActive={setTxFilter}></TxnType>
                            <Panel style={ { margin: '1rem auto', padding: '0', border: 'none' } }>
                                <TxnList transactions={ transactions } type={ txFilter }/>
                            </Panel>
                        </div>
                    </ContentWrapper>
                ) : ''
            }
            {
                tab === '2' ? (
                    <ContentWrapper>
                        <Panel style={ {
                            paddingTop: '15px',
                            border: 'none',
                            boxShadow: 'none',
                            background: 'none'
                        } }>
                            <PairList pairs={ allPairs }/>
                        </Panel>
                    </ContentWrapper>
                ) : ''
            }
            {
                tab === '3' ? (
                    <ContentWrapper>
                        <Panel style={ {
                            paddingTop: '15px',
                            border: 'none',
                            boxShadow: 'none',
                            background: 'none'
                        } }>
                            <TopTokenList tokens={ allTokens }/>
                        </Panel>
                    </ContentWrapper>
                ) : ''
            }
            <SupportText/>
        </PageWrapper>
    )
}

export default withRouter(GlobalPage)
